:root {
    --bg-color: rgb(20, 20, 20);
    --card-color: rgb(23, 23, 23);
}

.card {
    background-color: rgba(255, 255, 255, 0.1);
    border-radius: 10px;

    /*cursor: pointer;*/
    /*display: flex;*/
    /*height: 260px;*/
    /*flex-direction: column;*/

    position: relative;
    width: 300px;
    min-height: 100px;
    /*min-height: 260px; !* Add this property to set a minimum height *!*/

}

body {
    /*align-items: center;*/
    background-color: var(--bg-color);
    /*display: flex;*/
    /*height: 100vh;*/
    /*justify-content: center;*/
    /*margin: 0px;*/
    /*overflow: hidden;*/
    /*padding: 0px;*/
}

#cards {
    display: flex;
    /*flex-wrap: wrap;*/
    gap: 10px;
    /*max-width: 1000px;*/
    width: calc(100% - 20px);
}

#cards:hover > .card::after {
    opacity: 1;
}

.card:hover::before {
    opacity: 1;
}

.card::before,
.card::after {
    border-radius: inherit;
    content: "";
    height: 100%;
    left: 0px;
    opacity: 0;
    position: absolute;
    top: 0px;
    transition: opacity 500ms;
    width: 100%;
}

:root {
    --mouse-x: 0;
    --mouse-y: 0;
}

.card::before {
    background: radial-gradient(
            800px circle at var(--mouse-x) var(--mouse-y),
            rgba(255, 255, 255, 0.06),
            transparent 40%
    );
    z-index: 3;
}

.card::after {
    background: radial-gradient(
            600px circle at var(--mouse-x) var(--mouse-y),
            rgba(255, 255, 255, 0.4),
            transparent 35%
    );
    z-index: 1;
}

.card > .card-content {
    background-color: var(--card-color);
    border-radius: inherit;
    display: flex;
    flex-direction: column;
    flex-grow: 1;
    inset: 1px;
    padding: 10px;
    position: absolute;
    z-index: 2;
}

.hidderLeft{
    height: 50px;
    width: 70px;
    background-color: #141414;
}

.card-info-wrapper {
    align-items: center;
    display: flex;
    flex-grow: 2;
    justify-content: flex-start;
    /*padding: 0px 0px;*/
}

.card-info {
    align-items: flex-start;
    display: flex;
    gap: 5px;
}

.card-info > i {
    font-size: 1em;
    height: 20px;
    line-height: 20px;
}

.card-info-title > h3 {
    font-size: 1.1em;
    line-height: 10px;
}

.card-info-title > h4 {
    color: rgba(255, 255, 255, 0.5);
    font-size: 0.85em;
    margin-top: 8px;
}


